.partnersContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 100px;
  align-items: flex-start;
  padding-bottom: 100px;
  align-items: center;
  background-repeat:round;
}

.partnersHeader {
  color: white;
}

.partnersHeaderStyle{
  font-size: 50px;
  font-weight: bold;
}

.partnersText {
  font-size: 24px;
}


.partnersLogoContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 80%;
  margin-top: 25px;
}

.partnersLogoStyle {
  max-width: 200px;
  padding:36px;
}

@media (max-width: 1700px) {
  .partnersLogoContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 90%;
  }

  .partnersHeaderStyle{
    font-size: 50px;
    font-weight: bold;
  }

  .partnersText {
    font-size: 36px;
  }


  .partnersContainer {
    display: flex;
    flex-direction: column;
