.footerContainer {
  display: flex;
  flex-direction: row;
  justify-content: row;
  align-items: flex-start;
  padding-top: 50px;
  padding-bottom: 100px;
  font-size: 16px;
  background-color: #1E1357;
}

.footerLogoContainer {
  width: 17%;
}
.footerLogo {
  width: 150px;
}

.contactusContainer {
  padding-top: 20px;
  width: 22%;
  color: #B3B6F4;

}

.contactUsHeaderBoldStyle {
  font-size: 36px;
  font-weight: bold ;
  margin-right: 10px;
}

.contactUsText {
  text-align: left;
}

.contactUsHeaderTextStyle{
  font-size: 16px;
}

.contactUsHeader {
  display: flex;
  flex-direction: row;
}

.addressHeaderStyle {
  font-size: 36px;
  font-weight: bold;

}

.addressContainer {
  display: flex;
  flex-direction: column;
  width: 22%;
  color: #B3B6F4;
  padding-top: 20px;

}

.emailContainer {
  width: 22%;
  color: #B3B6F4;
  padding-top: 20px;

}

.emailHeader {
  font-size: 36px;
  font-weight: bold;

}

.emailStyle {
  cursor: pointer;
}

.addressTextStyle {
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
  font-size: 16px;
}

.socialContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 17%;

}

.socialContainerStyle {
  display: flex;
  flex-direction: row;
  
}


.socialIconImage {
  max-width: 30px;
  cursor: pointer;
  padding-top: 30px;
}


@media (max-width: 1400px) {
  .footerContainer {
    display: flex;
    flex-direction: row;
    justify-content: row;
    align-items: flex-start;
    padding-top: 50px;
    padding-bottom: 100px;
    font-family: 14px;
    background-color: #1E1357;
  }
  .contactUsHeaderBoldStyle {
    font-size: 30px;
    font-weight: bold ;
    margin-right: 10px;
  }
    
  .addressHeaderStyle {
    font-size: 30px;
    font-weight: bold;
  
  }

    .emailHeader {
      font-size: 30px;
      font-weight: bold;
    
    }

    .socialIconImage {
      max-width: 30px;
      cursor: pointer;
      padding-top: 30px;
    }
}


