.collectionContainer {
  display: flex;
  flex-direction: row;
  padding-top: 100px;
  background-color: #B3B6F4;
  justify-content: center;
  align-items: center;
  color:#1E1357 ;
}

.collectionBannerContainer {
  display: flex;
  flex-direction: column;
  width: 50%;
  align-items: center;
  margin-bottom: -10px;
}

.collectionBannerHeader {
font-size: 50px;
font-weight: bold;
max-width: 75%;
text-align: left ;
}

.collectionBannerText {
  font-size: 20px;
  max-width: 50%;
  text-align: left  ;
}

.collectionBannerImage {
  margin-top: 50px;
}

.collectionBannerImageStyle{
  width: 75%;
}

.collectionContent {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.collectionContentContainer{
  width: 75%;
  margin-bottom: 40px;
}

.collectionContentHeader{
  font-size: 20px;
  text-align: left;
  margin-bottom: 15px;
  font-weight: bold;
  cursor: pointer;
}

.collectionContentText{
  font-size: 16px;
  text-align: left;
}

@media (max-width: 1400px) {
  .collectionBannerHeader {
    font-size: 60px;
    font-weight: bold;
    max-width: 80%;
    text-align: left ;
    }
    
    .collectionBannerText {
      font-size: 30px;
      max-width: 80%;
      text-align: left  ;
    }

    .collectionBannerImageStyle{
      width: 100%;
    }
}



@media only screen and (max-width: 450px) {
  .collectionContainer {
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    background-color: #B3B6F4;
    justify-content: center;
    align-items: center;
    color:#1E1357 ;
    padding-bottom: 30px;
  }

  .collectionBannerContainer {
    display: flex;
    flex-direction: column;
    width: 80%;
    align-items: center;
    margin-bottom: 10px;
  }

  .collectionBannerImage {
    display: none;
    }

  .collectionBannerHeader {
    font-size: 36px;
    font-weight: bold;
    max-width: 100%;
    text-align: left ;
    }
    
    .collectionBannerText {
      font-size: 18px;
      margin-top: 20px;
      max-width: 100%;
      text-align: left  ;
      line-height: 24px;
    }

    .collectionBannerImageStyle{
      width: 100%;
    }

    .collectionContent {
      display: flex;
      flex-direction: column;
      width: 80%;
      margin-top: 25px;
    }

    .collectionContentContainer{
      width: 100%;
      margin-bottom: 20px;
    }

    .collectionContentText{
      font-size: 14px;
      text-align: left;
      line-height: 20px;
    }
}

@media only screen and (max-width: 600px) {
  .collectionContainer {
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    background-color: #B3B6F4;
    justify-content: center;
    align-items: center;
    color:#1E1357 ;
    padding-bottom: 30px;
  }

  .collectionBannerContainer {
    display: flex;
    flex-direction: column;
    width: 80%;
    align-items: flex-start;
    margin-bottom: 10px;
  }

  .collectionBannerImage {
    display: none;
    }

  .collectionBannerHeader {
    font-size: 36px;
    font-weight: bold;
    max-width: 100%;
    text-align: left ;
    }
    
    .collectionBannerText {
      font-size: 18px;
      margin-top: 20px;
      max-width: 100%;
      text-align: left  ;
      line-height: 24px;
    }

    .collectionBannerImageStyle{
      width: 100%;
    }

    .collectionContent {
      display: flex;
      flex-direction: column;
      width: 80%;
      margin-top: 25px;
    }

    .collectionContentContainer{
      width: 100%;
      margin-bottom: 20px;
    }

    .collectionContentText{
      font-size: 14px;
      text-align: left;
      line-height: 20px;
    }
}

