.exploreContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 100px;
  padding-bottom: 100px;
  justify-content: center;
  background-color: #1E1357;
}

.exploreContentContainer {
  display: flex;
  flex-direction: row;
  width: 80%;
}

.exploreImageStyle {
  width: 20%;
  padding: 25px 
}

.exploreContent {
  display: flex;
  flex-direction: row;
}

.exploreContentTextContainer {
  display: flex;
  justify-content: center;
  width: 40%;
}

.exploreContentText {
  display: flex;
  font-size: 60px;
  font-weight: bold;
  color: white;
  text-align: left;
  align-items: center;
  width: 80%;
  line-height: 80px;

}

@media (max-width: 1400px) {
  .exploreContentContainer {
    display: flex;
    flex-direction: row;
    width: 90%;
  }

  .exploreContentText {
    display: flex;
    font-size: 40px;
    font-weight: bold;
    color: white;
    text-align: left;
    align-items: center;
    width: 80%;
    line-height: 50px;
  
  }

}
@media only screen and (max-width: 850px) {
  .exploreContentContainer {
    display: flex;
    flex-direction: row;
    width: 100%;
  }

  .exploreImageStyle {
    width: 20%;
    padding: 20px 
  }

  .exploreContentText {
    display: flex;
    font-size: 40px;
    font-weight: bold;
    color: white;
    text-align: left;
    align-items: center;
    width: 80%;
    line-height: 50px;
  
  }

}

@media only screen and (max-width: 600px) {
