    display: flex;
    font-size: 40px;
    font-weight: bold;
    color: white;
    text-align: left;
    align-items: center;
    width: 80%;
    line-height: 50px;
  }

  .exploreContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 50px;
    justify-content: center;
    background-color: #1E1357;
  }

  .exploreContentText {
    display: flex;
    font-size: 18px;
    font-weight: bold;
    color: white;
