.utilityContainer {
    background-color: white;
    flex-direction: column;
    display: flex;
    align-items: center;
    padding-top: 100px;
    color: #1E1457;
    background-size: 75%;
}

.utilityBanner {
    display: flex;
    text-align: left;
    flex-direction: row;
    justify-content: center;
}


.utilityBannerText {
    font-size: 50px;
    align-content: flex-start;
    display: flex;
    flex-direction: row;
    font-weight: bold ;
}
.utilityContent {
    display: flex;
    flex-direction: row;
    margin-top: 50px;
    align-items: center;   
    max-width: 75%;
    justify-content: space-between;
}


.utilityBox {
    border: 2px hidden #1E1457;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-size: contain;
    background-position: center;
    width: 25%;
    height: 400px;
}


.utilityBoxHeader {
    font-weight: bold;
    font-size: 20px;
    margin-top: 40px;
    margin-bottom: 40px;
}

.utilityBoxContent {
    font-size: 16px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    width: 80%;  
    line-height: 30px;
}

@media (max-width: 1400px) {
    .utilityBanner {
        display: flex;
        text-align: left;
        flex-direction: row;
        justify-content: center;
        max-width: 85%;
    }    

    .utilityBannerText {
        font-size: 50px;
        align-content: flex-start;
        display: flex;
        flex-direction: row;
        font-weight: bold ;
    }

    .utilityContent {
        display: flex;
        flex-direction: row;
        margin-top: 0px;
        align-items: center;   
        max-width: 90%;
        justify-content: space-between;
    }
  }

@media only screen and (max-width: 450px) {

    .utilityContainer {
        background-color: white;
        flex-direction: column;
        display: flex;
        align-items: center;
        padding-top: 50px;
        color: #1E1457;
        background-size: 75%;
    }
    .utilityBanner {
        display: flex;
        text-align: left;
        flex-direction: row;
        justify-content: center;
        max-width: 80%;
    }    

    .utilityBannerText {
        font-size: 36px;
        align-content: flex-start;
        display: flex;
        flex-direction: row;
        font-weight: bold ;
    }

    .utilityBox {
        border: 1px solid #1E1457;
        background-image: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        width: 90%;
        height: 200px;
        margin-bottom: 25px;
    }

    .utilityContent {
        display: flex;
        flex-direction: column;
        margin-top: 36px;
        align-items: center;   
        max-width: 70%;
        justify-content: space-between;
    }

    .utilityBoxHeader {
        font-weight: bold;
        font-size: 18px;
        margin-top: 0px;
        margin-bottom: 10px;
    }
    
    .utilityBoxContent {
        font-size: 14px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        text-align: center;
        width: 50%;  
        line-height: 20px;
        margin-bottom: 20px;
    }
  
}

