.ownershipContainer {
  display: flex;
  flex-direction: row;
  background-color: #1E1357;
  color: white;
  padding-bottom: 100px ;
}

.ownershipHeaderImage{
  display: flex;
  justify-content: flex-start;
  width: 40%; 
}

.ownershipImage {
  display: flex;
  width: 75%;  
}

.ownershipContentContainer{
  display: flex;
  flex-direction: column;
  max-width: 50%;
  color: #B3B6F4;
  justify-content:center ;
  
}

.ownershipContentHeader {
  display: flex;
  font-size: 50px;
  font-weight: bold;
  max-width: 60%;
  text-align: left;
}

.ownershipContent {
  display: flex;
  flex-direction: column;
  align-content: left;
  margin-top: 50px;
  max-width: 60%;
  font-size: 16px;
}

.ownershipContentText {
  display: flex;
  text-align: left;
  margin-bottom: 30px;
  line-height: 35px;
}

