.bannerContainer {
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 100px;
  padding-bottom: 100px;

}

.bannerContent {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.headerlogo {
  max-width: 400px;
}


.bannerHeader {
  font-size: 50px;
  font-weight: bolder;
  display: flex;
  flex-direction: column;
  text-align: left;
  max-width: 50%;

}

.headerContent {
  display: flex;
  text-align: left;
  color: white;
  width: 80%; 

}

.bannerText {
  font-size: 24px;
  font-weight: normal;
  display: flex;
  text-align: left;
  margin-bottom: 50px;
  max-width: 60%;
  max-width: 50%;

}

.bannerButton {
  display: flex;
  align-items: center;
  padding: 15px;
  margin-right: 50px;
  cursor: pointer;
  color: black;
  border: 1px solid white;
  background-color: white;
  border-radius: 20px;
  font-weight: normal;
  font-size: 12px;
}

.button {
  max-height: 50px;
  margin-right: 10px;
}

.buttonText{
  margin-right: 5px;
  display: flex;
}

.textBold {
  font-weight: bold;
  margin-right: 30px;
}


.bannerGIF {
  max-height: 700px;
  border-radius: 10px;
}

.bannerButtonContainer {
  display: flex;
  max-width: 80%;
  margin-top: 40px;
}

