.App {
  text-align: center;
  font-family: "Raleway";
  display: flex;
  flex-direction: column;
  }

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.font-loader {
  font-family: 'Raleway';
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 450px) {
  .App {
    text-align: center;
    font-family: "Raleway";
    display: flex;
    flex-direction: column;
    margin-right: -10px;
  }
}

